<script>
//dashed
import Dice from "_c/dice";
import web3Call from "@/metaMask/getWeb3.js";
import countTo from "vue-count-to";

export default {
  name: "dthContent",

  data() {
    return {
      // allData: [],
      take: 0.0,
      tokenTake: 0.0,
      allData: web3Call.alldata,
      myData: web3Call.mydata,
      // rangklist: Object.values(web3Call.rangData),
      rangklist: [],
      DiceStrat: false,
      maximineValue: 10,
      diceActive: new Array(6),
      dicimgArray: [
        require("../assets/img/dice-1.png"),
        require("../assets/img/dice-2.png"),
        require("../assets/img/dice-3.png"),
        require("../assets/img/dice-4.png"),
        require("../assets/img/dice-5.png"),
        require("../assets/img/dice-6.png")
      ],
      diceActiveCount: 1,
      dice16X0: "",
      data1: [
        {
          userID: "1",
          name: "John Brown",
          age: 18,
          address: "New York No. 1 ",
          date: "2016-10-03"
        },
        {
          userID: "1",
          name: "Jim Green",
          age: 24,
          address: "London No. 1 ",
          date: "2016-10-01"
        },
        {
          userID: "1",
          name: "Joe Black",
          age: 30,
          address: "Sydney No. 1 ",
          date: "2016-10-02"
        },
        {
          userID: "1",
          name: "Jon Snow",
          age: 26,
          address: "Ottawa No. ",
          date: "2016-10-04"
        }
      ],
      //重复复点击
      isdouble: true
    };
  },

  components: {
    // 模板
    Dice,
    countTo
    // audioPlayer
  },
  watch: {
    myData(val, oldval) {
      console.debug(val.length !== oldval);
      if (val !== oldval) {
        web3Call.userBalance().then(res => {
          console.debug(res);
          this.take = res;
        });
      }
      // this.allData=oldval.sort((a, b) => b.data - a.data)
    }
  },
  computed: {
    // 计算属性
    columns1() {
      let _self = this;
      return [
        {
          title: this.$t("Ranking"),
          key: "userID",
          width: 60,
          type: "index"
        },
        {
          title: this.$t("Player"),
          key: "name",
          tooltip: true,
          ellipsis: true
        },
        {
          title: this.$t("Total"),
          key: "Betting"
        },
        {
          title: this.$t("reward"),
          key: "Profit",
          sortType: "desc",
          sortable: true
        }
      ];
    },
    columns2() {
      return [
        {
          title: this.$t("Tiem"),
          key: "data",
          // width: 80
          sortType: "desc",
          sortable: true
        },
        {
          title: this.$t("Player"),
          key: "name",
          ellipsis: true,
          tooltip: true
        },
        {
          title: this.$t("Betting"),
          key: "Betting"
        },
        {
          title: this.$t("Odds"),
          key: "Odds"
        },
        {
          title: this.$t("Profit"),
          key: "Profit"
        },
        {
          title: this.$t("Quiz"),
          key: "Quiz"
        }
      ];
    },

    _value10Number() {
      let b = 0;
      for (let i = this.diceActive.length - 1; i >= 0; i--) {
        if (this.diceActive[i]) {
          b += 1;
        }
        b = b << 1;
      }
      b = b >> 1;
      // console.debug(b)
      // return b
      return b;
    },
    WinChance() {
      return Number(((this.diceActiveCount / 6) * 100).toFixed(2));
    },
    Odds() {
      return Number(((6 / this.diceActiveCount) * 0.98).toFixed(2));
    },
    Profit() {
      return Number((this.Odds * this.maximineValue).toFixed(2));
    }
  },

  methods: {
    // 方法
    handelRorClick(e) {
      console.debug(e);
      window.open("https://etherscan.io/tx/" + e.transactionHash);
    },
    withdraw() {
      //提现
      web3Call.takeWithdraw();
    },
    handelMultiple(vale) {
      console.debug("用户的小狐狸的钱"+this.tokenTake)
      if(vale!=="min"&&this.maxinmieValue >=this.tokenTake){return}
      if(!this.tokenTake)return
      switch (vale) {
        case 2:
          this.maximineValue *= 2;
          break;
        case 5:
          this.maximineValue *= 5;
          break;
        case "max":
            this.maximineValue = this.tokenTake;
          break;
        default:
          this.maximineValue = 1;
          break;
      }
    },
    handelDiceSelect(index) {
      if (!this.diceActive[index]) {
        if (this.diceActiveCount >= 5) {
          return;
        }
        this.diceActiveCount += 1;
      } else {
        if (this.diceActiveCount <= 1) return;
        this.diceActiveCount -= 1;
      }
      this.diceActive.splice(index, 1, !this.diceActive[index]);
    },

    _arrayCnt(arr) {
      let newArr = [...new Set(arr)];
      let newarr2 = new Array(newArr.length).fill(0);
      for (let p = 0; p < newArr.length; p++) {
        for (let j = 0; j < arr.length; j++) {
          if (newArr[p] == arr[j]) {
            newarr2[p]++;
          }
        }
      }

      for (var m = 0; m < newArr.length; m++) {
        if (newArr[m]) {
          return newarr2[m] == 5;
        }
      }
    },
    stopAnmationDice(index) {
      if (!this.isdouble) return;
      this.isdouble = false;
      this.$Loading.start();

      var _self = this;
      if (this.tokenTake < this.maximineValue) {
        _self.isdouble = true;
        _self.$Loading.finish();
        return this.$Message.error("Insufficient account balance!");
      }
      web3Call.approveAndCall(
        this.maximineValue,
        this._value10Number,
        error => {
          if (error) {
            _self.isdouble = true;
            _self.$Loading.finish();
          }
        }
      );
    },
    _isSuccess(event) {
      let Number16 = parseInt(event.returnValues.chosen);
      // console.debug(ArrayNumber)
      let quiz = [];
      for (let i = 1; i <= 6; i++) {
        if (Number16 % 2 === 1) {
          quiz.push(i);
        }
        Number16 = Number16 >> 1;
      }
      console.debug(quiz);
      return quiz.includes(parseInt(event.returnValues.result));
    },
    _initUserAll() {
      web3Call.userBalance().then(res => {
        console.debug("用户赢的钱", res);

        this.take = Number(res).toFixed(2);
      });
      web3Call.balanceOf().then(res => {
        console.debug("用户余额", res);
        this.tokenTake = res;
      });
      web3Call.rangkList(res => {
        this.rangklist = Object.values(res);
      });

      //查看余额
      this.userBalanceStop = setInterval(() => {
        web3Call.userBalance().then(res => {
          // alert(4)

          this.take = Number(res).toFixed(2);
        });
        web3Call.balanceOf().then(res => {
          this.tokenTake = res;
        });
      }, 4000);
    }
  },
  created: function() {
    // console.debug('2-created 创建完成');
    this.diceActive.fill(false);
    this.diceActive[0] = true;
    console.debug(this.$t("Player"));
    // if (window.web3.currentProvider.selectedAddress) {
    this._initUserAll();
    // }
    var _self = this;
    web3Call.AllBets((error, event) => {
      if (error) {
        alert(error);

        throw error;
      }
      console.debug("当前的骰子的点数", event.returnValues.result);
      _self.isdouble = true;
      _self.$Loading.finish();
      let a = _self._isSuccess(event);

      _self.$refs.child1.handelAnimatStop(Number(event.returnValues.result), a);
    });
  },
  beforeCreate: function() {},

  // beforeMount:function(){
  //     console.debug('3-beforeMount 挂载之前');
  // },
  // mounted:function(){
  //     console.debug('4-mounted 被创建');
  //      this._initUserAll();
  // },
  // beforeUpdate:function(){
  //     console.debug('5-beforeUpdate 数据更新前');
  // },
  // updated:function(){
  //     console.debug('6-updated 被更新后');
  // },
  // activated:function(){
  //     console.debug('7-activated');
  // },
  // deactivated:function(){
  //     console.debug('8-deactivated');
  // },
  // beforeDestroy:function(){
  //     console.debug('9-beforeDestroy 销毁之前');
  // },
  destroyed: function() {
    // console.debug('10-destroyed 销毁之后')
    clearInterval(this.userBalanceStop);
  }
};
</script>
